angular.module('ppApp').directive('pdf', function() {
    return {
        restrict: 'E',
        link: function(scope, element, attrs) {
            var url = scope.$eval(attrs.src);
            console.log(url)
            element.replaceWith('<object width="100%" height="400px" type="application/pdf" data="' + url + '"></object>');
        }
    };
});