angular.module('ppApp').controller('questionnaireCtrl', questionnaireCtrl);

questionnaireCtrl.$inject = ['$state', '$stateParams', '$scope', '$timeout', '$http']

function questionnaireCtrl($state, $stateParams, $scope, $timeout, $http) {

    var vm = this;

    var patentFromJson = angular.fromJson($stateParams.patent);
    vm.patent = patentFromJson.data;
    vm.correctPatent = correctPatent;
    vm.incorrectPatent = incorrectPatent;
    vm.confirmEntity = confirmEntity;
    vm.confirmAmendments = confirmAmendments;
    vm.enterReference = enterReference;
    vm.generateForm = generateForm;
    vm.form1200Incorrect = form1200Incorrect;
    vm.form1200Correct = form1200Correct;
    vm.patentFound = true;
    vm.displayQuestionnaire = true;
    vm.costPanel = false;

    vm.rates = {
        today: 1.5,
        yDay: 1.35,
        lstWk: 1.55,
        lstMnth: 1.95
    }

    vm.question = {
        q1: true
    };

    vm.transaction = {
        feeUI: {
            supplmentaryFee: 678,
            designationFee: 11700,
            examFee: 1635,
            filingFee: 120,
            claimsFee: 900,
            subTotal_USD: 15003
        }
    }

    function incorrectPatent() {
        $state.go('form1200', {}, {reload: true});
        //modal potentially displayed
    }

    function correctPatent() {
        vm.question.q2 = true;
        vm.activeTab = 2;
    }

    function confirmEntity() {
        vm.question.q3 = true;
        vm.activeTab = 3;
    }    

    function confirmAmendments() {
        vm.question.q4 = true;
        vm.activeTab = 4;
    }

    function enterReference() {
        vm.question.q5 = true;
        vm.activeTab = 5;
    }

    function generateForm() {
        vm.displayQuestionnaire = false;
        vm.generateLoader = true;
        $http.get('hardcodedpdffolder/certificates/dummyCertificateNumber1.pdf')
        .then(
            function(response){
                vm.doc_details = {
                    file_url: 'hardcodedpdffolder/certificates/dummyCertificateNumber1.pdf'
                }
                vm.generateLoader = false;
                vm.generatedPdf = true;      
            },
            function(errResponse){

            }
        )
    }

    function form1200Correct() {
        vm.generatedPdf = false;
        vm.patentFound = false;
        vm.costPanel = true;

    }

    function form1200Incorrect() {
        $state.go('form1200');

    }

}