angular.module('ppApp').controller('portfolioCtrl', portfolioCtrl);

portfolioCtrl.$inject = ['$rootScope', 'portfolioService'];

function portfolioCtrl($rootScope, portfolioService) {

    var vm = this;

    $rootScope.page = 'Portfolio';

    portfolioService.fetchPatents()
    .then(
        function(response){
            vm.tableData = response.patents;
        },
        function(errResponse){

        }
    )

}